.error {
  min-height: 100vh;
  position: relative;
  padding: 240px 0;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  text-align: center;
  margin-top: 70px;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.error__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.errorContent {
  position: absolute;
  top: 42%;
  left: 50%;
  width: 100%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.errorMessage {
  text-align: center;
  color: #131316;
}

.messageTitle {
  font-family: 'Jet Brains Mono', sans-serif;
  font-weight: 900;
  text-transform: uppercase;
  letter-spacing: 5px;
  font-size: 3.6rem;
  padding-bottom: 40px;
  max-width: 500px;
  margin: 0 auto;
}

.messageText {
  font-family: 'Jet Brains Mono', sans-serif;
  line-height: 42px;
  font-size: 72px;
  padding: 0 60px;
  max-width: 680px;
  margin: auto;
}

.errorNav {
  max-width: 600px;
  margin: 40px auto 0;
  text-align: center;
}

.errorNav > button {
  background: none;
  color: #131316;
  border: 2px solid #131316;
  font-weight: 700;
}

.errorNav > button:hover {
  box-shadow: 0px 0px 10px 10px rgba(0, 204, 204, 0.2);
  border: 2px solid #00cccc;
  color: white;
}

.eNav__form {
  position: relative;
  height: 45px;
  overflow: hidden;
  width: 170px;
  display: inline-block;
  vertical-align: top;
  border: 1px solid #212121;
  padding-left: 10px;
  padding-right: 46px;
}

.eNav__icon {
  position: absolute;
  right: 15px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  color: #212121;
  -webkit-transition: color 0.25s ease;
  transition: color 0.25s ease;
}

.eNavLink {
  height: 45px;
  line-height: 45px;
  width: 170px;
  display: inline-block;
  vertical-align: top;
  margin: 0 15px;
  border: 1px solid #131316;
  color: #131316;
  text-decoration: none;
  font-family: 'Jet Brains Mono', sans-serif;
  text-transform: uppercase;
  font-size: 11px;
  letter-spacing: 0.1rem;
  position: relative;
  overflow: hidden;
}

.eNavLink:before {
  content: '';
  height: 200px;
  background: #212121;
  position: absolute;
  top: 70px;
  right: 70px;
  width: 260px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  -webkit-transform: rotate(50deg);
  transform: rotate(50deg);
}

.eNavLink:after {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  z-index: 999;
  position: relative;
}

.eNavLink:after {
  content: 'Home Page';
}

.eNavLink:hover:before {
  top: -60px;
  right: -50px;
}

.eNavLink:hover {
  color: #fff;
}

.eNavLink:nth-child(2):hover:after {
  color: #fff;
}

.error__social {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  left: 20px;
  z-index: 10;
}

.e-social__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.e-social__icon {
  padding-bottom: 30px;
}

.e-social__icon:last-child {
  padding-bottom: 0;
}

.e-social__link {
  color: #fff;
  -webkit-transition: all 0.25s ease;
  transition: all 0.25s ease;
  display: block;
}

.e-social__link:hover {
  opacity: 0.7;
}

.lampWrap {
  position: relative;
  top: -100px;
}

.lamp {
  position: absolute;
  left: 0px;
  right: 0px;
  top: 0px;
  margin: 0px auto;
  width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  transform-origin: center top;
  animation-timing-function: cubic-bezier(0.08, 0.69, 1, 0.54);
  animation: move 3.1s infinite;
}

@keyframes move {
  0% {
    transform: rotate(17deg);
  }
  50% {
    transform: rotate(-17deg);
  }
  100% {
    transform: rotate(17deg);
  }
}

.cable {
  width: 8px;
  height: 248px;
  background-image: linear-gradient(rgb(32 148 218 / 70%), rgb(132, 0, 172)),
    linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7));
}

.cover {
  width: 200px;
  height: 80px;
  background: #0bd5e8;
  border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  position: relative;
  z-index: 200;
}

.inCover {
  width: 100%;
  max-width: 200px;
  height: 20px;
  border-radius: 100%;
  background: #08ffff;
  position: absolute;
  left: 0px;
  right: 0px;
  margin: 0px auto;
  bottom: -9px;
  z-index: 100;
}
.inCover .bulb {
  width: 50px;
  height: 50px;
  background-color: #08fffa;
  border-radius: 50%;
  position: absolute;
  left: 0px;
  right: 0px;
  bottom: -20px;
  margin: 0px auto;
  -webkit-box-shadow: 0 0 15px 7px rgba(0, 255, 255, 0.8),
    0 0 40px 25px rgba(0, 255, 255, 0.5),
    -75px 0 30px 15px rgba(0, 255, 255, 0.2);
  box-shadow: 0 0 25px 7px rgb(127 255 255 / 80%),
    0 0 64px 47px rgba(0, 255, 255, 0.5), 0px 0 30px 15px rgba(0, 255, 255, 0.2);
}

.light {
  width: 200px;
  height: 0px;
  border-bottom: 900px solid rgb(44 255 255 / 24%);
  border-left: 50px solid transparent;
  border-right: 50px solid transparent;
  position: absolute;
  left: 0px;
  right: 0px;
  top: 270px;
  margin: 0px auto;
  z-index: 1;
  border-radius: 90px 90px 0px 0px;
}

.error {
  overflow: hidden;
  max-height: 100vh;
}

@keyframes appearance {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
